"use client";

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import io from "socket.io-client";
// Socket type is not exported in v2 as named export easily, define loose type or omit
type Socket = any;

// Types matching SignalsView
export interface SignalData {
    id: number;
    provider: string;
    providerRank: string;
    pair: string;
    action: 'BUY' | 'SELL';
    pips: number;
    price: string;
    sl: string;
    tp1: string;
    tp2: string;
    tp3: string;
    category: string;
    timestamp: string;
    winRate: number;
    lotSize: number;
    profit?: number;
}

export interface SignalResult {
    id: string | number;
    pair: string;
    type: string;
    entryPrice: string;
    closePrice: string;
    netProfit: number;
    pips: number;
    lotSize: number;
    timestamp: string;
    provider: string;
}

interface TradingContextType {
    socket: Socket | null;
    signals: SignalData[];
    results: SignalResult[];
    prices: Record<string, number>;

    // Account State
    isConnected: boolean;
    accountBalance: number;
    accountEquity: number;

    // Platform Connection States
    mt5Connected: boolean;
    dxConnected: boolean;
    matchTraderConnected: boolean;
    tradeLockerConnected: boolean;

    // Actions
    setMt5Connected: (connected: boolean) => void;
    setDxConnected: (connected: boolean) => void;
    setMatchTraderConnected: (connected: boolean) => void;
    setTradeLockerConnected: (connected: boolean) => void;
    setAccountBalance: (balance: number) => void;
    setAccountEquity: (equity: number) => void;

    // Signal Actions
    approveSignal: (signalId: number) => Promise<void>;
}

const TradingContext = createContext<TradingContextType | undefined>(undefined);

export const useTrading = () => {
    const context = useContext(TradingContext);
    if (!context) {
        throw new Error("useTrading must be used within a TradingProvider");
    }
    return context;
};

export const TradingProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
    const [socket, setSocket] = useState<Socket | null>(null);
    const [signals, setSignals] = useState<SignalData[]>([]);
    const [results, setResults] = useState<SignalResult[]>([]);
    const [prices, setPrices] = useState<Record<string, number>>({});

    // Account State
    const [accountBalance, setAccountBalance] = useState(0);
    const [accountEquity, setAccountEquity] = useState(0);

    // Platform Connection States
    const [mt5Connected, setMt5Connected] = useState(false);
    const [dxConnected, setDxConnected] = useState(false);
    const [matchTraderConnected, setMatchTraderConnected] = useState(false);
    const [tradeLockerConnected, setTradeLockerConnected] = useState(false);

    const isConnected = mt5Connected || dxConnected || matchTraderConnected || tradeLockerConnected;

    // Initialize Socket
    useEffect(() => {
        const newSocket = io("http://localhost:8000", {
            transports: ["websocket", "polling"],
            path: "/socket.io", // creating v2 socket
            // withCredentials: true // v2 might need this or not depending on CORS
        });

        newSocket.on("connect", () => {
            console.log("TradingContext Socket Connected:", newSocket.id);
        });

        newSocket.on("price_update", (data: any) => {
            setPrices(prev => ({
                ...prev,
                [data.symbol]: (data.bid + data.ask) / 2
            }));
        });

        newSocket.on("new_signal", (newSignal: any) => {
            console.log("Context New Signal:", newSignal);
            const signal: SignalData = {
                id: newSignal.id || Date.now(),
                provider: newSignal.provider || "Unknown",
                providerRank: newSignal.providerRank || "Pro",
                pair: newSignal.pair,
                action: newSignal.action as 'BUY' | 'SELL',
                pips: newSignal.pips || 0,
                price: newSignal.price,
                sl: newSignal.sl,
                tp1: newSignal.tp1,
                tp2: newSignal.tp2,
                tp3: newSignal.tp3,
                category: newSignal.category || "FOREX",
                timestamp: newSignal.timestamp || "Just now",
                winRate: newSignal.winRate || 70,
                lotSize: newSignal.lotSize || 0.01
            };
            setSignals(prev => [signal, ...prev]);
        });

        newSocket.on("signal_result", (result: any) => {
            console.log("Context Signal Result:", result);
            const formattedResult: SignalResult = {
                id: result.id || Date.now(),
                pair: result.pair,
                type: result.type,
                entryPrice: result.entryPrice,
                closePrice: result.closePrice,
                netProfit: result.netProfit,
                pips: result.pips || 0,
                lotSize: result.lotSize || 0.01,
                timestamp: result.timestamp || new Date().toISOString(),
                provider: result.provider || "Verstige AI"
            };

            setResults(prev => [formattedResult, ...prev]);
            setSignals(prev => prev.filter(s => s.id !== result.id && s.id !== Number(result.id)));
        });

        newSocket.on("account_update", (data: any) => {
            console.log("Context Account Update:", data);
            if (data.data) {
                setAccountBalance(data.data.balance);
                setAccountEquity(data.data.equity);
                // We can also set margin, freeMargin etc if we add state for them
                // For now, this meets the requirement
                if (!isConnected) {
                    // Auto set connected if receiving data
                    setMt5Connected(true);
                }
            }
        });

        setSocket(newSocket);

        return () => {
            newSocket.disconnect();
        };
    }, []);

    // Mock initial data for demo purposes if empty
    useEffect(() => {
        if (signals.length === 0) {
            setSignals([
                { id: 1, provider: 'Daniel PRO', providerRank: 'Elite', pair: 'XAUUSD', action: 'BUY', pips: 0, price: '2034.50', sl: '2028.00', tp1: '2040.00', tp2: '2045.00', tp3: '2055.00', category: 'GOLD', timestamp: '2 min ago', winRate: 87, lotSize: 0.5 },
                { id: 2, provider: 'Elite FX', providerRank: 'Pro', pair: 'EURUSD', action: 'SELL', pips: 0, price: '1.0950', sl: '1.1000', tp1: '1.0900', tp2: '1.0850', tp3: '1.0800', category: 'FOREX', timestamp: '5 min ago', winRate: 72, lotSize: 1.0 },
            ]);
        }
    }, []);

    const approveSignal = async (signalId: number) => {
        console.log("Approving signal via context:", signalId);
        // Implement execution logic here or keep it in components for now and just update state?
        // Ideally execution logic moves here, but for now let's just log it.
        // We will migrate execution logic in the refactor step.
    };

    return (
        <TradingContext.Provider value={{
            socket,
            signals,
            results,
            prices,
            isConnected,
            accountBalance,
            accountEquity,
            mt5Connected,
            dxConnected,
            matchTraderConnected,
            tradeLockerConnected,
            setMt5Connected,
            setDxConnected,
            setMatchTraderConnected,
            setTradeLockerConnected,
            setAccountBalance,
            setAccountEquity,
            approveSignal
        }}>
            {children}
        </TradingContext.Provider>
    );
};
